import {BarLine, BarLineChartData, PriceDistributionBarLineChart, PriceDistributionItem} from "../types/market";

export const takeIntervalAxisSetting = (list: number[]): { max: number; min: number; interval: number } => {
    const SPLIT_NUMBER = 5
    const min = Math.floor(list.reduce((m, num) => m > num ? num : m, 0) * 0.9)
    const max = Math.ceil(list.reduce((m, num) => m < num ? num : m, 0) * 1.1)
    const interval = list ? (max - min) / SPLIT_NUMBER : 0
    return {min, max, interval}
}

export function formatBarLineAxis(axis: string[], category?: string): string[] {
    if (!category) return axis;
    const copy = [...axis];
    if (category === 'webcam') {
        copy[0] = '10~20';
    }
    if (category === 'document camera') {
        copy[0] = '17~30';
    }
    if (category === 'video projector') {
        copy.splice(0, 1);
        copy[0] = '21~40';
    }
    return copy
}

export function filterFirstWhenVideoProjector(data: number[], category?: string) {
    if (category === 'video projector') {
        const copy = [...data]
        copy.splice(0, 1)
        return copy
    }
    return data
}


export function formatBarLineData(data: PriceDistributionBarLineChart | null, category?: string): BarLineChartData | null {
    if (!data) return null;

    const axis = formatBarLineAxis(data.info.x_axis, category)
    const series: BarLine[] = [
        {
            type: 'bar',
            name: 'Review Quantity',
            data: filterFirstWhenVideoProjector(
                data.data.map((it: PriceDistributionItem) => it.sold),
                category
            ),
            yAxisIndex: 0,
        },
        {
            type: 'bar',
            name: 'Product Model',
            data: filterFirstWhenVideoProjector(
                data.data.map((it: PriceDistributionItem) => it.count),
                category
            ),
            yAxisIndex: 0,
        },
        {
            type: 'line',
            name: 'Revenue(million USD)',
            data: filterFirstWhenVideoProjector(
                data.data.map((it: PriceDistributionItem) => it.revenue),
                category
            ),
            yAxisIndex: 1,
        }
    ];
    return {axis, series}
}

