import React, {useEffect, useRef} from 'react'
import * as echarts from 'echarts/core'
import {LineChart, BarChart, LineSeriesOption, BarSeriesOption} from "echarts/charts";
import {
    TitleComponent,
    TitleComponentOption,
    GridComponent,
    GridComponentOption,
    TooltipComponent,
    TooltipComponentOption,
    LegendComponent,
    LegendComponentOption,
    DataZoomComponent,
    DataZoomComponentOption
} from "echarts/components";
import {CanvasRenderer} from "echarts/renderers";
import {HiDownload} from 'react-icons/hi'
import {downloadPng} from "../utils/download";
import {BarLine, BarLineChartData} from "../types/market";
import {takeIntervalAxisSetting} from "../utils/chart";

interface BarLineChartProps {
    width?: string;
    height?: string;
    title?: string;
    titleFontSize?: number;
    data: BarLineChartData | null;
    names?: string[];
    onClick?(value: string): void;
}

type ECOption = echarts.ComposeOption<BarSeriesOption | LineSeriesOption | TitleComponentOption | GridComponentOption | TooltipComponentOption | LegendComponentOption | DataZoomComponentOption>;

echarts.use([LineChart, BarChart, TitleComponent, GridComponent, TooltipComponent, LegendComponent, CanvasRenderer, DataZoomComponent])

function BarLineChart(props: BarLineChartProps) {
    const chartRef = useRef<HTMLDivElement | null>(null);
    const {width = '100%', height = '400px', title = 'Title', titleFontSize = 24, data = null, names = [], onClick} = props;

    function getLegend(name: string) {
        if (!data) return name;

        const target = data.series.find((it: BarLine) => it.name === name)
        if (!target) return name;

        if (name === 'Revenue(million USD)') {
            const total = target.data.reduce((result, num) => result + num, 0).toFixed(2);
            return `${name}, total: ${total}`
        }
        const total = target.data.reduce((result, num) => result + num, 0);
        return `${name}, total: ${total}`
    }

    const leftAxisSetting = takeIntervalAxisSetting(data ? data.series.filter((it: BarLine) => it.yAxisIndex === 0).reduce((arr: number[], list: BarLine) => [...arr, ...list.data], []) : [])
    const rightAxisSetting = takeIntervalAxisSetting(data ? data.series.filter((it: BarLine) => it.yAxisIndex === 1).reduce((arr: number[], list: BarLine) => [...arr, ...list.data], []) : [])

    const option: ECOption = {
        title: {
            text: title,
            textStyle: {
                fontSize: titleFontSize
            }
        },
        grid: {
            backgroundColor: 'white',
            top: 70,
            right: 70,
            left: 70,
            bottom: 115
        },
        legend: {
            bottom: 0,
            formatter(name) {
                return getLegend(name)
            }
        },
        dataZoom: [
            {
                show: true,
                realtime: true,
                bottom: 60
            },
            {
                type: 'inside',
                show: true,
                realtime: true,
                bottom: 60
            }
        ],
        tooltip: {
            trigger: 'axis'
        },
        xAxis: [
            {
                type: 'category',
                data: data ? data.axis : [],
            }
        ],
        yAxis: [
            {
                type: 'value',
                name: names[0] || 'Quantity',
                min: leftAxisSetting.min,
                max: leftAxisSetting.max,
                interval: leftAxisSetting.interval,
                show: !!names[0]
            },
            {
                type: 'value',
                name: names[1] || 'Value',
                min: rightAxisSetting.min,
                max: rightAxisSetting.max,
                interval: rightAxisSetting.interval,
                show: !!names[1]
            }
        ],
        series: data ? data.series : []
    }

    useEffect(() => {
        if (!chartRef.current) return

        const chart = echarts.init(chartRef.current, 'customed')
        chart.setOption(option)
        chart.on('click', (params) => {
            if(onClick && params && data) {
                onClick(data.axis[params.dataIndex])
            }
        })
    })

    const onDownload = async () => {
        if (!chartRef.current) return
        await downloadPng(chartRef.current, title)
    }

    return (
        <div className="relative">
            <HiDownload className="absolute right-0 top-0 text-3xl cursor-pointer z-30" onClick={onDownload}/>
            <div ref={chartRef} style={{width, height}}/>
        </div>
    )
}

export default BarLineChart
